.content {
    max-width: 547px;
    padding: 57px 0 0 55px;
    &__title {
        font-family: Montserrat;
        font-style: normal;
        font-weight: bold;
        font-size: 32px; }
    &__list {
        padding-top: 52px;
        display: flex;
        flex-direction: column; }
    &__item {
        position: relative;
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        font-family: Roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 14px; }
    &__checked {
        position: absolute;
        top: -2px;
        left: 0;
        width: 20px;
        height: 20px;
        border: 2px solid #E8E8E8;
        border-radius: 50%; }
    &__text {
        margin-left: 30px; }
    &__none {
        display: flex;
        justify-content: center;
        align-items: center;
        align-content: center;
        height: 100%;
        font-weight: 700;
        font-size: 24px;
        color: #767676; }

    &-form {
        max-width: 415px;
        flex-direction: column;
        &__text {
            height: 38px;
            width: 100%;
            padding-left: 14px; }
        &__btn {
            width: 145px;
            height: 34px; }
        &__close {
            width: 90px;
            height: 34px; }
        &__btn, &__close {
            margin-top: 16px; } } }


@media screen and (max-width: 900px) {
    .content {
        width: 73vw;
        padding-left: 15px;
        &__title {
            font-size: 20px; }
        &__list {
            padding-top: 52px;
            display: flex;
            flex-direction: column; }
        &__item {
            margin-bottom: 20px;
            display: flex;
            align-items: center;
            font-family: Roboto;
            font-style: normal;
            font-weight: 500;
            font-size: 14px; } } }

@media screen and (max-width: 670px) {
    .content {
        padding-left: 0;
        &-form {
            &__text {
                width: 60vw; }
            &__btn {
                width: 60vw; } } } }
